import { motion } from "framer-motion";
import styled from "styled-components";
import { useQuery } from "@tanstack/react-query";
import ReactPlayer from "react-player";
import Close from "../buttons/Close";
import { getTvVideos } from "../../api/tv";

const Wrapper = styled(motion.div)`
	top: 0;
	z-index: 11;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.9);
`;

const Container = styled(motion.div)`
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 11;
	margin: 0 auto;
`;

interface IProps {
	contentId: number;
}
interface IGetVideo {
	results: [
		{
			key: string;
		}
	];
}

function VideoPlay({ contentId }: IProps) {
	const { data: videoData } = useQuery<IGetVideo>(
		[`tvVideo`, `tvVideo_${contentId}`],
		() => getTvVideos(contentId)
	);

	const getURL = () => {
		if (videoData?.results[0]) {
			return `http://www.youtube.com/watch?v=${videoData.results[0].key}`;
		} else {
			return `http://www.youtube.com/watch?v=GV3HUDMQ-F8`;
		}
	};

	return (
		<Wrapper
			layout
			initial={{ opacity: 0 }}
			animate={{ opacity: 1 }}
			exit={{ opacity: 0 }}
			transition={{ ease: "easeOut", duration: 0.5 }}
		>
			<Container
				layout
				initial={{ opacity: 0 }}
				animate={{ opacity: 1 }}
				exit={{ opacity: 0 }}
				transition={{ ease: "easeOut", duration: 0.5 }}
			>
				<ReactPlayer
					url={getURL()}
					width="100vw"
					height="100vh"
					muted={true}
					playing={true}
				/>
				<Close isCenter={true} />
			</Container>
		</Wrapper>
	);
}

export default VideoPlay;
